@charset "utf-8";

article>h2{
 text-align: center;
}

article > input[type=text]{
  display: block;
  margin: 15px auto;
  width: 629px;
  height: 46px;
  padding: 0px 11px 1px 15px;
  border-radius: 4px;
  border: 1px solid rgb(221, 221, 221);
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: rgb(51, 51, 51);
  outline: none;
  box-sizing: border-box;
}

article > p:first-of-type{
  text-align: center;
  margin-left: 431px;
}
