@charset "UTF-8";

*{
    margin: 0px; padding: 0px;
}

ul,ol,li{
    list-style: none;
}
.mp_ul{
    display: flex;
    width: 1050px;
    margin: 0px auto;
}
.mp_ul li{
    width: 25%;
    overflow: hidden;
}
.mp_ul li img{
    width: 249px;
    height: 320px;
}

li img:hover{
    transform: scale(1.01);
    transition: all 0.3s ease-in-out 0s;
    overflow: hidden;
}
h2{
    color: rgb(51, 51, 51);
    font-size: 28px;
    line-height: 1.15;
    letter-spacing: -0.26px;
    font-weight: bold;
    text-align: center;
    margin-top: 66px;
    margin-bottom: 50px;
}
ul p{
    line-height: 32px;
}
ul p:nth-child(2){
    font-size: 14px;
    font-weight: bold;
}
.price:nth-child(3){
    color: #f00;
    font-weight: bold;
}
.price2:nth-child(4){
    color: #838383;
    font-weight: bold;
    font-size: 12px;
}
.btsimages{
    width: 249px;
    height: 320px;
}
